@import url("https://fonts.googleapis.com/css2?family=Fira+Sans:wght@500&family=Oxygen:ital,wght@0,300;0,400;1,300;1,400&family=Oxygen+Mono&display=swap");

h1, h2, h3, h4, h5, h6 {
  font-family: $headfont;
  font-weight: 500;
  letter-spacing: 4px;
}

@font-face {
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 300;
  src: url("/assets/fonts/OpenSans-Light.woff") format("woff"), url("/assets/fonts/OpenSans-Light.ttf") format("ttf");
}

@font-face {
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 400;
  src: url("/assets/fonts/OpenSans-Regular.woff") format("woff"), url("/assets/fonts/OpenSans-Regular.ttf") format("ttf");
}

@font-face {
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 500;
  src: url("/assets/fonts/OpenSans-Semibold.woff") format("woff"), url("/assets/fonts/OpenSans-Semibold.ttf") format("ttf");
}

@font-face {
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 700;
  src: url("/assets/fonts/OpenSans-Bold.woff") format("woff"), url("/assets/fonts/OpenSans-Bold.ttf") format("ttf");
}

