.committee {
  display: flex;
  flex-direction: row;
  gap: 1rem 0.5rem;
  flex-wrap: wrap;
  justify-content: center;
  align-items: start;
}

.committee-member {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 1rem 0.5rem;
  width: 20ch;
  gap: 0.5rem;
  text-align: center;

  &__image {
    border-radius: 100%;
    width: 75%;
  }


  &__email {
    a {
      color: $medium-text;
      transform: scale(1);
      transition:
        transform ease 200ms,
        color ease 200ms;

      &:hover {
        transform: scale(1.25);
        color: $blue;
      }
    }
  }

  &__socials {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 0.5em;
    font-size: 1.125rem;

    a {
      color: $medium-text;
      transform: scale(1);
      transition:
        transform ease 200ms,
        color ease 200ms;

      &:hover {
        transform: scale(1.25);
        color: $blue;
      }
    }
  }
}

.idea {
  border: 1px solid #999999;
  border-radius: 3px;
  padding: 10px;

  h2 {
    margin-top: 5px;
  }

  ul.idea__statuses {
    li.idea-status {
      list-style-type: none;
      margin: 10px 0;
      padding: 10px;

      h3 {
        margin-top: 5px;
      }

      &--in-progress {
        background-color: #fffdaa;
      }

      &--stalled {
        background-color: #ffd89e;
      }

      &--rejected {
        background-color: #ffa293;
      }

      &--completed {
        background-color: #c3ff99;
      }

      &--proposed {
        background-color: #e5e5e5;
      }
    }
  }
}

.btn, a.btn {
  border: 1px solid #999999;
  border-radius: 3px;
  color: #999;
  display: inline-block;
  font-size: 14px;
  font-weight: 400;
  letter-spacing: 2px;
  line-height: 20px;
  margin: 10px;
  padding: 10px 20px;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  transition: all .2s;

  &:hover {
    border: 1px solid #777777;
    color: #555555;
  }

  &--light {
    border: 1px solid #ffffff;
    color: #ffffff;
    &:hover {
      border: 1px solid #ffffff;
      color: #ffffff;
      background: rgba(255,255,255,.1);
    }
  }

  &--dark {
    border: 1px solid #555555;
    color: $dark-text;
    &:hover {
      border: 1px solid #000000;
      color: #000000;
      background: rgba(0,0,0,.1);
    }
  }
}
